.page {
  display: grid;
  grid-gap: 0;
  grid-template-areas:
    "logo   logo   hdrart"
    "midpad midpad midpad"
    "nav    main   main  "
    "nav    footer footer";
  grid-template-columns: 1fr 3fr 2fr;
  grid-template-rows: repeat(4, auto);
  margin: 0 auto;
  max-width: 1000px;
}

.spacer {
  grid-area: midpad;
  height: 2em;
  padding: 0;
}
